<template>

  <div>
    <el-row>
      <el-col :span="4">
        <el-button>当日请求:</el-button>
      </el-col>
      <el-col :span="4">
        <el-button type="primary">{{request_url_day_count}}</el-button>
      </el-col>
    </el-row>
    <el-row style="margin-top: 10px">
      <el-col :span="4">
        <el-button>总请求:</el-button>
      </el-col>
      <el-col :span="4">
        <el-button type="primary">{{request_url_all_count+1}}</el-button>
      </el-col>
    </el-row>
    <el-row style="margin-top: 10px">

      <el-carousel :interval="4000" type="card" height="200px">
        <el-carousel-item v-for="item in 6" :key="item">
          <div class="banner_p">
            <img src="http://po8c2o94f.bkt.clouddn.com/%E5%BE%AE%E4%BF%A1%E5%9B%BE%E7%89%87_20190304163943.png"
                 width="100" height="100"/>
          </div>
        </el-carousel-item>
      </el-carousel>

    </el-row>

    <el-row style="margin-top: 10px">

      <el-col :span="10">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>{{info.name}}</span>
          </div>
          <div v-for="o in info_datas" :key="o" class="text item">
            {{o }}
          </div>
        </el-card>
      </el-col>

      <el-col :span="10">

        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>{{location}}</span>
          </div>

          <div v-for="o in weather_datas" :key="o" class="text item">
            {{o }}
          </div>
        </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>



  const axios = require('axios');


  export default {
    name: "Tab1",
    data() {
      return {
        request_url_day_count: 100,
        request_url_all_count: 1000,
        info: '',
        info_datas: [],
        ip: '',
        location: '',
        weather_datas: [],
      }
    },
    mounted() {
      console.log('mounted ok begin http request')


      axios.get('https://ipapi.co/json/').then(data => {
        let str = JSON.stringify(data, null, 2)
        console.log(data)
        let ip = data['data']['ip']
        console.log('ip  ' + ip)
        this.ip = ip;


        this.getWeatherByIp(ip)

      });


      setInterval(this.getData, 10000);

    },

    methods: {
      getData() {
        this.$jsonp('https://api.shenjian.io', {
          appid: '5a0bdcbd6e118e7817b537aaf587df53',
          codes: '002421'
        }).then(response => {
          console.log(response)
          this.info = response.data;
          this.info_datas = [];
          this.info_datas.push('当前价格 ' + this.info.price)
          this.info_datas.push('开盘价格 ' + this.info.pre_close)
          this.info_datas.push('波动 ' + ((this.info.price - this.info.pre_close) / this.info.pre_close * 100).toFixed(2) + '%')
        }).catch(e => {

        })
      },
      getLoctioinByIp(ip) {
        this.$jsonp('https://api.shenjian.io', {
          appid: 'b751fa0b782cb95573d3afb92831634d',
          ip: ip
        }).then(response => {
          console.log(response)
          this.location = response.data.geo
        }).catch(e => {

        })
      },
      getWeatherByIp(ip) {
        this.$jsonp('https://api.shenjian.io/weather/ip', {
          appid: '9d38603cff55b01fb89794eadfc92411',
          ip: ip
        }).then(response => {
          console.log(response)

          this.location = '当前城市 '+response.data.city

          this.weather_datas = [];
          this.weather_datas.push(response.data.now[0].now_feelst)
          this.weather_datas.push(response.data.now[0].now_icomfort)
          this.weather_datas.push(response.data.now[0].now_wind_power)
        }).catch(e => {

        })
      }
    }

  }


</script>

<style scoped>

  .el-button {
    width: 100px;
  }

  .el-col {
    display: flex;
    margin-left: 10px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .banner_p {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 100%;
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>



