<template>
  <div class="main">
    <H1>Login</H1>
    <div class="main_input_div">
      <span>用户名</span>
      <el-input
        placeholder="请输入内容"
        v-model="username"
        clearable>
      </el-input>
    </div>
    <div class="main_input_div">
      <span>密码</span>
      <el-input
        placeholder="请输入密码"
        v-model="pwd"
        show-password
        clearable>
      </el-input>
    </div>
    <el-button style="width: 10rem;margin-left: 10rem" @click="login">登录</el-button>
  </div>
</template>

<script>
  export default {
    name: "Login",
    data() {
      return {
        username: '',
        pwd: ''
      }
    },
    methods: {
      login() {

        if(!this.username || !this.pwd){
          this.$message('请输入用户名和密码')
          return
        }

        Bmob.User.login(this.username,this.pwd).then(res => {
          console.log(res)

          this.$router.push({ name: 'manager', params: { userId: '123' }})

        }).catch(err => {
          console.log(err)
          this.$message(err.error)
        });
      }
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex
    flex-direction column
    justify-content center
    align-items center
    width 100%
    height 100%
    background #bfcbd9

    .main_input_div
      display: flex
      justify-content center
      align-items center

      width 50%
      padding 1rem

      span
        width 10rem

        display flex
        justify-content center

      .el-input
        width 20rem
        margin-left 1rem

</style>



