<template>
  <el-container>
    <el-aside width="200px">


      <h5>后台管理</h5>
      <router-link :to="{ name: 'home'}" target="_blank">首页</router-link>
      <el-menu
        :default-active="defaultActive"
        class="el-menu-vertical-demo"
        @select="handleSelect"
        router
      >

        <el-menu-item index="manager_add">
          <i class="el-icon-circle-plus-outline"></i>
          <span slot="title">添加商品</span>
        </el-menu-item>
        <el-menu-item index="manager_motify">
          <i class="el-icon-setting"></i>
          <span slot="title">修改商品</span>
        </el-menu-item>

      </el-menu>


    </el-aside>
    <el-main>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </el-main>
  </el-container>
</template>

<script>
  export default {
    name: "Manager",
    data() {
      return {}
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    },
    mounted() {
      this.$router.push({name: 'manager_add'})
    },
    computed: {
      defaultActive: function () {
        console.log('defaultActive')
        return this.$route.path.replace('/', '');
      }
    },
  }
</script>

<style scoped>
  .el-container {
    height: 100%;
  }

  .el-aside {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
</style>



