<template>

  <div class="main">
    <el-button @click="updateAction">保存更新</el-button>
    <div v-for="(item, index) of list_datas" class="main_list">
      <PicDataMotify :obj="item" @click_base="pic_update"/>
    </div>
  </div>
</template>

<script>

  import PicDataMotify from "./PicDataMotify";



  export default {
    name: "ManageMotify",
    components: {PicDataMotify},
    data() {
      return {
        list_datas: []
      }
    },
    mounted() {
      const query = Bmob.Query("Pic_Data");
      query.find().then(res => {
        // console.log(res)
        for (let item of res) {
          let urls=item.url.split(",")
          this.list_datas.push({'url':urls,'description':item.description,'url0':urls[0]});

          // this.list_datas.push({'url': item.url, 'text': item.description})
        }

      }).catch(error => {
        console.log(error)
      })
    },
    methods: {
      updateAction() {
        const query = Bmob.Query("Pic_Data");
        query.find().then(todos => {
          todos.destroyAll().then(res => {
            // 成功批量修改
            console.log(res, 'ok')

            this.addResultDatas();

          }).catch(err => {
            console.log(err)
          });
        })
      },
      addResultDatas(){
        const queryArray = new Array();
        // 构造含有50个对象的数组
        for (var i = 0; i < this.list_datas.length; i++) {
          var queryObj = Bmob.Query('Pic_Data');
          queryObj.set('url', this.list_datas[i].url);
          queryObj.set('description', this.list_datas[i].text);
          queryArray.push(queryObj);
        }

        // 传入刚刚构造的数组
        Bmob.Query('Pic_Data').saveAll(queryArray).then(result => {
          console.log(result);
          this.loading = false
          this.$message('修改成功,去首页看看最新商品吧');
        }).catch(err => {

          this.$message(err);
          this.loading = false
        });
      },
      pic_update(obj) {
        console.log(obj)
        this.list_datas.remove(obj)
      },
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex
    flex-direction column
    align-items center
    padding 1rem

    .main_list

      width 80%


</style>



