<template>
  <div>

    <div v-for="(val, key, index) in data_maps">
      <DataCard :showNumber="3" :objs="val" :ot="index"/>
    </div>
  </div>
</template>

<script>


  import DataCard from "./DataCard";


  export default {
    name: "Tab3",
    components: {DataCard},

    data() {
      return {
        data_maps: {},

      }
    },

    mounted() {
      this.getShowDatas()
    },
    methods: {
      getShowDatas() {

        const query = Bmob.Query("Pic_Data");
        query.find().then(res => {
          console.log(res)

          let index = 0;
          let map_index = 0;
          let temp_obj;
          let temp_maps = {}
          for (let item of res) {
            if (index % 3 === 0) {
              temp_obj = [];
              temp_maps[map_index + ''] = temp_obj;
              map_index++;
            }
            let urls=item.url.split(",")

            temp_obj.push({'url':urls,'description':item.description,'url0':urls[0]});
            index++;
          }

          this.data_maps = Object.assign({}, this.data_maps, temp_maps)

          console.log(this.data_maps)

        }).catch(error=>{
          console.log(error)
        });

        // this.axios.get('https://bing.getlove.cn/latelyBingImageStory')
        //   .then(data => {
        //
        //   }).catch(error => {
        //   console.log(error)
        // })
      }
    }
  }
</script>

<style scoped>

</style>



