<template>


  <el-row :gutter="10" class="base_main">
    <div v-for="(data, index) in objs" :key="index">
      <el-col :span="item_span">
        <el-card :body-style="{ padding: '5px' }">
          <div class="main" v-viewer>
            <img ref="img" v-bind:src="data.src_url" class="image">
            <div class="close">
              <i class="el-icon-close" @click="deletePic(data)"/>
            </div>
          </div>
        </el-card>
      </el-col>
    </div>
  </el-row>


</template>

<script>
  export default {
    name: "PicS_Render",
    props: {
      objs: Array
    },

    watch: {
      objs: function update(new_value, old_value) {
        console.log(new_value[0])
      }
    },


    mounted() {
      // console.log(this.objs)
      // console.log(this.objs.length )
      // this.src_update()

    },

    data() {
      return {
        item_span: 4,
      }
    },

    methods: {
      deletePic(data) {
        // console.log('deletePic '+index)
        this.$emit('click_base', data);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .base_main
    width 100%
    .main
      display flex;
      background #bfcbd9
      flex-direction row
      width 100%
      justify-content center
      .image {
        width: 92%;
        height: 12rem;
      }

      .close
        flex 1
        align-self flex-start
        display flex
        justify-content flex-end
        padding 0.45rem

  div img {
    cursor: pointer;
    transition: all 0.6s;
  }

  div img:hover {
    transform: scale(2.1);
  }

  div i:hover {
    transform: scale(1.8);
  }
</style>



