<template>
  <div class="main">
    <div class="image_main">
      <img v-bind:src="obj.url0" class="image">
    </div>
    <el-input style="margin-left:1rem" v-model="obj.text"></el-input>
    <div class="close">
      <i class="el-icon-close" @click="deletePic"/>
    </div>
  </div>
</template>

<script>
  export default {
    name: "PicDataMotify",
    props: {
      obj: Object,
    },
    mounted() {
      console.log(this.obj.url)
    },
    methods: {
      deletePic() {
        console.log('deletePic')
        this.$emit('click_base', this.obj);
      }
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex;
    margin-top 1rem

    background #bfcbd9
    flex-direction row
    justify-content flex-start
    .image_main
      width 30%
      padding 1rem
      display flex
      align-items center
      .image {
        width: auto;
        height: 7rem;
      }
    .close

      flex 1
      align-self flex-start
      display flex
      justify-content flex-end
      padding 0.45rem

  div div img {
    cursor: pointer;
    transition: all 0.6s;
  }


  div i:hover {
    transform: scale(1.8);
  }

</style>



