<template>

  <el-row :gutter="10">
    <div v-for="(data, index) in objs" :key="index">
      <el-col :span="item_span">
        <el-card :body-style="{ padding: '5px' }">

          <!--<div class="demo-image__preview">-->
            <!--<el-image-->
              <!--class="image"-->
              <!--style="width: 100px; height: 100px"-->
             <!---->
              <!--:src="data.url0"-->
              <!--:preview-src-list="data.urls">-->
            <!--</el-image>-->
          <!--</div>-->

          <div  v-viewer >
            <img v-bind:src="data.url0" class="image">
          </div>
          <div class="content">
            <span>{{ data.description}}</span>
          </div>
        </el-card>
      </el-col>
    </div>
  </el-row>
</template>

<script>
  export default {
    name: "DataCard",
    props: {
      showNumber: Number,
      objs: Array,
      ot: Number
    },
    data() {
      return {

        item_span: 24 / this.showNumber,

      }
    },
    mounted() {
      console.log(this.objs.urls)

    },


  }
</script>

<style scoped>


  .el-card {
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    margin-top: 10px;
  }

  .image {

    width: 40%;
    height: 40%;

  }

  div img {

    cursor: pointer;
    transition: all 0.3s;
  }

  div img:hover {
    transform: scale(2.1);
  }

  .content {
    padding: 14px;
    display: flex;
    flex-direction: column;
    justify-content: center;

  }
</style>



