<template>
  <div class="main">
    <div class="image_main" v-for="(val ,key,index ) in obj.urls">
      <img v-bind:src="val" class="image">
    </div>
    <span style="margin-left:1rem">{{obj.text}}</span>
    <div class="close">
      <i class="el-icon-close" @click="deletePic"/>
    </div>
  </div>
</template>

<script>
  export default {
    name: "PicData",
    props: {
      obj: Object,
    },
    mounted() {
      console.log(this.obj)
    },
    methods: {
      deletePic() {
        console.log('deletePic')
        this.$emit('click_base', this.obj);
      }
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex;

    width 100%
    background #bfcbd9
    flex-direction row
    justify-content flex-start

    .image_main
      width 50%
      /*background #67c23a*/
      display flex
      padding 1rem

      .image {

        width: auto;
        height: 7rem;
      }

    .close

      flex 1
      align-self flex-start
      display flex
      justify-content flex-end
      padding 0.45rem

  div div img {
    cursor: pointer;
    transition: all 0.6s;
  }


  div i:hover {
    transform: scale(1.8);
  }

</style>



